.intro {
  color: $white;

  background-image: url("../img/pcboard1x.png"), url("../img/bg-intro1x.jpg");
  background-repeat: no-repeat;
  background-position: 85.3% 120px, center top;
  background-size: 37% auto, cover;

  background-color: $bg;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/pcboard2x.png"), url("../img/bg-intro2x.jpg");
  }
}

.intro__wrapper {
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 25px;
}

.intro__text-row {
  position: relative;

  padding-top: 314px;
  padding-bottom: 276px;
}

.intro h2 {
  margin: 0;
  margin-bottom: 30px;

  font-family: $roboto;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 5.5px;
  text-transform: uppercase;
}

.intro p {
  position: relative;

  width: 720px;
  margin: 0;
  margin-bottom: 40px;
  padding-left: 97px;

  font-size: 22px;
  line-height: 34px;

  &::before {
    content: "";
    position: absolute;
    top: 16px;
    left: 2px;


    width: 66px;
    height: 2px;

    background-color: $white;
  }
}

.intro__link {
  display: inline-block;
  margin-left: 97px;
  padding: 18px 45px;

  font-family: $roboto;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: $white;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  vertical-align: middle;

  background-color: $blue;
  border-radius: 5px;

  transition: all 0.3s;

  &:hover,
  &:focus {
    background-color: darken($blue, 10%);
  }

  &:active {
    background-color: darken($blue, 20%);
  }
}

.intro__link-text {
  outline: none;
}

.intro__link-mobile {
  display: none;
}

.intro__scroll-down {
  position: absolute;
  bottom: 127px;
  left: -35px;

  display: block;

  font-size: 14px;
  line-height: 16px;
  color: rgba($white, 0.5);

  transform: rotate(-90deg);
  transition: all 0.3s;

  cursor: pointer;

  &:hover,
  &:focus {
    color: rgba($white, 0.3);
  }
}

@media (max-width: $large-width) {
  .intro h2 {
    font-size: 58px;
  }

  .intro p {
    width: 650px;
  }
}

@media (max-width: $tablet-width) {
  .intro {
    background-image: url("../img/pcboard-tablet1x.png"), url("../img/bg-intro1x.jpg");
    background-position: 100% 0, center top;
    background-size: 330px 466px, cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/pcboard-tablet2x.png"), url("../img/bg-intro2x.jpg");
    }
  }

  .intro__text-row {
    padding-top: 190px;
    padding-bottom: 163px;
    padding-left: 60px;
  }

  .intro h2 {
    width: 60%;
    margin-bottom: 27px;

    font-size: 60px;
    line-height: 70px;
    letter-spacing: 4.5px;
  }

  .intro p {
    width: 450px;
    margin-bottom: 54px;
    padding-left: 8%;

    font-size: 18px;
    line-height: 28px;

    &::before {
      width: 8%;
      left: 5px;
    }
  }

  .intro__link {
    margin-left: 8%;
    padding: 18px 31px;
  }

  .intro__scroll-down {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .intro {
    background-image: url("../img/psboard-mobile1x.png"), url("../img/bg-intro1x.jpg");
    background-position: 100% 0, center top;
    background-size: 294px 437px, cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/psboard-mobile2x.png"), url("../img/bg-intro2x.jpg");
    }
  }

  .intro__wrapper {
    padding: 0 5%;
  }

  .intro__text-row {
    padding: 0;
    padding-top: 285px;
    padding-bottom: 70px;

    text-align: center;
  }

  .intro h2 {
    margin-bottom: 25px;

    font-size: 40px;
    line-height: 48px;
    letter-spacing: 3px;
    text-align: left;
  }

  .intro p {
    width: 280px;
    margin-bottom: 40px;
    padding-left: 50px;

    font-size: 16px;
    line-height: 25px;
    text-align: left;

    &::before {
      width: 33px;
    }
  }

  .intro__link {
    margin: 0;
  }

  .intro__link-text {
    display: none;
  }

  .intro__link-mobile {
    display: inline-block;
  }
}
